import React, { useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import {
  MinusIcon, PencilIcon, PlusIcon, TrashIcon,
} from '@heroicons/react/outline'
import FloatingBtn from '../global/FloatingBtn'
import ModalComp from '../global/ModalComp'
import ButtonComp from '../global/ButtonComp'
import SignatureComp from '../global/SignatureComp'
import TabMenu from '../global/TabMenu'
import mItem from '../../utils/mItem'

function InspectionForm({
  inspection, checklist, inspector, getForm, getFormInspector,
}) {
  const [form, setForm] = useState({})
  const [formChecklist, setFormChecklist] = useState({})
  const [formChecklistKet, setFormChecklistKet] = useState([])
  const [formInspector, setFormInspector] = useState({})

  // show hide general form
  const [showGeneralForm, setShowGeneralForm] = useState(true)

  // menampung array id parent yang show
  const [showChild, setShowChild] = useState([])
  // untuk kasi tau sudah diinit apa belum show child nya
  const [isInitShowChild, setIsInitShowChild] = useState(false)

  const [showModalAckSignature, setShowModalAckSignature] = useState(false)
  const [showModalInspectorSignature, setShowModalInspectorSignature] = useState(false)
  const [aksiInspectorSignature, setAksiInspectorSignature] = useState('tambah')

  const ackSignatureComp = useRef()
  const inspectorSignatureComp = useRef()

  const typeOperationForm = [
    {
      id: 1,
      text: 'Passanger',
    },
    {
      id: 2,
      text: 'Cargo',
    },
    {
      id: 3,
      text: 'Other',
    },
  ]
  const actionTakenForm = [
    {
      id: '3c',
      text: 'Aircraft grounded by DGCA inspector',
    },
    {
      id: '3b',
      text: 'Corrective actions before flight',
    },
    {
      id: '3a',
      text: 'Restrictions on the aircraft',
    },
    {
      id: '2',
      text: 'Information to the operator and authority',
    },
    {
      id: '1',
      text: 'Information to the PIC',
    },
    {
      id: '0',
      text: 'No remarks',
    },
  ]

  const tabMenu = ['General', 'Checklist', 'Inspector(s) Signature']
  const [tabMenuActive, setTabMenuActive] = useState('General')

  function handleSubmit(e) {
    e.preventDefault()
    const formattedForm = {}
    Object.keys(form).forEach((f) => {
      formattedForm[f] = form[f]
    })
    // cek yg checkbox (type operation, action taken)
    const typeOfOperationArr = []
    const actionTakenArr = []
    Object.keys(form).forEach((f) => {
      // jika includes dan bernilai true
      if (f.includes('type_of_operation_') && form[f]) {
        typeOfOperationArr.push(f.substring('type_of_operation_'.length))
      }
      if (f.includes('action_taken_') && form[f]) {
        actionTakenArr.push(f.substring('action_taken_'.length))
      }
    })
    formattedForm.type_of_operation = JSON.stringify(typeOfOperationArr).replace('[', '').replace(']', '').replace(/"/g, '')
    formattedForm.action_taken = JSON.stringify(actionTakenArr).replace('[', '').replace(']', '').replace(/"/g, '')

    // format form cehcklist
    const formattedChecklist = []
    mItem.forEach((f) => {
      // check apakah item itu ada dikirim apa tidak
      const hasItem = formChecklist[`${f.id_item}_check`] || formChecklist[`${f.id_item}_remark`]
      if (hasItem) {
        formattedChecklist.push({
          id_item: f.id_item,
          check: (formChecklist[`${f.id_item}_check`]) ? 1 : 0,
          remark: (formChecklist[`${f.id_item}_remark`]) ? 1 : 0,
          cat: formChecklist[`${f.id_item}_cat`] || '',
          keterangan: formChecklist[`${f.id_item}_keterangan`] || '',
        })
      }
    })
    getForm({
      tabMenuActive,
      formattedForm,
      formattedChecklist,
    })
    setForm({})
    setFormChecklist({})
    setFormChecklistKet([])
    // console.log(form)
  }

  function handleChange(e) {
    const { name } = e.target
    const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value
    setForm((values) => ({ ...values, [name]: value }))
  }
  function handleChangeChecklist(e) {
    const { name } = e.target
    const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value
    setFormChecklist((values) => ({ ...values, [name]: value }))

    // fungsi set form checklist ket
    const formChecklistKetNew = []
    formChecklistKet.forEach((f) => {
      formChecklistKetNew.push(f)
    })
    const [idItem, type] = name.split('_')
    const item = mItem.find((g) => g.id_item === parseInt(idItem, 10))
    // eslint-disable-next-line max-len
    const formChecklistKetExist = formChecklistKet.findIndex((f) => f.id_item === parseInt(idItem, 10)) > -1
    if (type === 'remark') {
      if (formChecklistKetExist && !value) {
        // jika exist, dan tidak centang remark, hapus
        // eslint-disable-next-line max-len
        formChecklistKetNew.splice(formChecklistKetNew.findIndex((f) => f.id_item === parseInt(idItem, 10)), 1)
      } else if (!formChecklistKetExist && value) {
        // jika tidak exist dan centang remark, tambah
        formChecklistKetNew.push({
          id_item: item.id_item,
          kode: item.kode,
        })
      }
    }
    setFormChecklistKet(formChecklistKetNew)

    // // fungsi set form checklist
    // const formChecklistKetNew = []
    // Object.keys(formChecklist).forEach((f) => {
    //   console.log(f)
    //   const [idItem, type] = f.split('_')
    //   if (type === 'remark' && formChecklist[f]) {
    //     const item = mItem.find((g) => g.id_item === parseInt(idItem, 10))
    //     formChecklistKetNew.push({
    //       id_item: item.id_item,
    //       kode: item.kode,
    //     })
    //   }
    // })
    // setFormChecklistKet(formChecklistKetNew)
  }
  function handleChangeInspector(e) {
    const { name, value } = e.target
    setFormInspector((values) => ({ ...values, [name]: value }))
  }

  function setFormInspection() {
    if (form.id_ramp_inspection !== inspection.id_ramp_inspection) {
      const formInit = {}
      Object.keys(inspection).forEach((e) => {
        formInit[e] = inspection[e]
      })
      if (inspection.type_of_operation) {
        const typeOperationCb = inspection.type_of_operation.split(',')
        typeOperationCb.forEach((e) => {
          formInit[`type_of_operation_${e}`] = 1
        })
      }
      if (inspection.action_taken) {
        const actionTakenCb = inspection.action_taken.split(',')
        actionTakenCb.forEach((e) => {
          formInit[`action_taken_${e}`] = 1
        })
      }
      setForm(formInit)
    }
  }

  function setFormChecklistShow() {
    if (!('init' in formChecklist)) {
      const formChecklistNew = {}
      checklist.forEach((e) => {
        formChecklistNew[`${e.id_item}_check`] = e.check
        formChecklistNew[`${e.id_item}_remark`] = e.remark
        formChecklistNew[`${e.id_item}_cat`] = e.cat
        formChecklistNew[`${e.id_item}_keterangan`] = e.keterangan
      })
      formChecklistNew.init = 1
      setFormChecklist(formChecklistNew)
    }
  }

  function changeAckSignature() {
    setShowModalAckSignature(true)
  }

  function closeModalAckSignature() {
    setShowModalAckSignature(false)
  }

  function getAckSignature() {
    // ackSignatureImage = ackSignatureComp.current.getImage()
    // console.log(ackSignatureImage)
    // console.log(ackSignatureFile)
    setForm((values) => ({
      ...values,
      ack_signature_base64: ackSignatureComp.current.getImage(),
    }))
    setShowModalAckSignature(false)
  }

  function addInspectorSignature() {
    setFormInspector({})
    setAksiInspectorSignature('create')
    setShowModalInspectorSignature(true)
  }
  function editInspectorSignature(val) {
    setFormInspector(val)
    setAksiInspectorSignature('edit')
    setShowModalInspectorSignature(true)
  }

  function closeModalInspectorSignature() {
    setShowModalInspectorSignature(false)
  }
  function getInspectorSignature() {
    getFormInspector({
      aksiInspectorSignature,
      formInspector,
      signature: inspectorSignatureComp.current.getImage(),
    })
    setShowModalInspectorSignature(false)
  }

  function toggleChild(id) {
    // gak tau cara cepetnya
    const showChildNew = []
    showChild.forEach((e) => {
      showChildNew.push(e)
    })
    if (showChildNew.includes(id)) {
      // hapus dari toggle parent, supaya parent dibawahnya ke hide juga
      const toggleParent = mItem.find((e) => e.id_item === id).toggle_parent.split(',')
      toggleParent.forEach((e) => {
        showChildNew.splice(showChildNew.findIndex((f) => f === parseInt(e, 10)), 1)
      })
    } else {
      showChildNew.push(id)
    }
    setShowChild(showChildNew)
  }

  useEffect(() => {
    if ('id_ramp_inspection' in inspection) {
      // console.log('tes')
      setFormInspection()
      setFormChecklistShow()

      // inisialisasi semua form terbuka
      const defaultChecklistOpen = false
      if (!isInitShowChild && defaultChecklistOpen) {
        const showChildInit = []
        mItem.forEach((e) => {
          if (parseInt(e.is_has_child, 10) === 1) {
            showChildInit.push(e.id_item)
          }
        })
        setShowChild(showChildInit)
        setIsInitShowChild(true)
      }
    }
  })

  return (
    <div>
      <form onSubmit={handleSubmit}>
        {
          ('id_ramp_inspection' in inspection) ? (
            <TabMenu
              menu={tabMenu}
              active={tabMenuActive}
              setTabMenuActive={(val) => setTabMenuActive(val)}
            />
          ) : ''
        }

        <div
          className={(tabMenuActive === 'General') ? 'block' : 'hidden'}
        >
          <div className="flex">
            <button
              type="button"
              className="flex-1"
              onClick={() => setShowGeneralForm(!showGeneralForm)}
            >
              <h4 className="text-xl text-left py-4 dark:text-gray-300">General Information</h4>
            </button>
            <button
              type="button"
              className="ml-auto dark:text-gray-300"
              onClick={() => setShowGeneralForm(!showGeneralForm)}
            >
              {
                (showGeneralForm) ? <MinusIcon className="w-5 h-5" /> : <PlusIcon className="w-5 h-5" />
              }

            </button>
          </div>
          <hr />
          <div
            className={`${(showGeneralForm) ? 'block' : 'hidden'}`}
          >
            <div
              className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-4"
            >
              {/* date */}
              <div className="py-2">
                <label htmlFor="form-date" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  1) Date
                </label>
                <input
                  type="date"
                  id="form-date"
                  className="t-form"
                  name="date"
                  value={form.date || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* time */}
              <div className="py-2">
                <label htmlFor="form-time" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  2) Time
                </label>
                <input
                  type="time"
                  id="form-time"
                  className="t-form"
                  name="time"
                  value={form.time || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* place */}
              <div className="md:col-span-2 py-2">
                <label htmlFor="form-place" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  3) Place
                </label>
                <input
                  type="text"
                  id="form-place"
                  className="t-form"
                  name="place"
                  value={form.place || ''}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              {/* operator */}
              <div className="md:col-span-2 py-2">
                <label htmlFor="form-operator" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  4. Operator
                </label>
                <input
                  type="text"
                  id="form-operator"
                  className="t-form"
                  name="operator"
                  value={form.operator || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* state */}
              <div className="py-2">
                <label htmlFor="form-state" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  5) State
                </label>
                <select
                  id="form-state"
                  className="t-form"
                  name="state"
                  value={form.state || ''}
                  onChange={handleChange}
                  required
                >
                  <option value="">Select State</option>
                  <option value="INDONESIA">INDONESIA</option>
                </select>
              </div>
              {/* aoc no */}
              <div className="py-2">
                <label htmlFor="form-aoc-no" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  6) AOC No
                </label>
                <input
                  type="text"
                  id="form-aoc-no"
                  className="t-form"
                  name="aoc_no"
                  value={form.aoc_no || ''}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              {/* route from */}
              <div className="py-2">
                <label htmlFor="form-route-from" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  7) Route From
                </label>
                <input
                  type="text"
                  id="form-route-form"
                  className="t-form"
                  name="route_from"
                  value={form.route_from || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* flight no from */}
              <div className="py-2">
                <label htmlFor="form-flight-no-from" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  8) Flight No
                </label>
                <input
                  type="text"
                  id="form-flight-no-from"
                  className="t-form"
                  name="flight_no_from"
                  value={form.flight_no_from || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* route to */}
              <div className="py-2">
                <label htmlFor="form-route-to" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  9) Route to
                </label>
                <input
                  type="text"
                  id="form-route-to"
                  className="t-form"
                  name="route_to"
                  value={form.route_to || ''}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* flight no to */}
              <div className="py-2">
                <label htmlFor="form-flight-no-to" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  10) Flight No
                </label>
                <input
                  type="text"
                  id="form-flight-no-to"
                  className="t-form"
                  name="flight_no_to"
                  value={form.flight_no_to || ''}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              {/* type of operation */}
              <div className="py-2 col-span-2">
                <label htmlFor="form-type-of-operation-1" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  11) Type of Operation (Ref OPSPECS)
                </label>
                <div className="mt-1 border border-gray-300 dark:border-gray-700 rounded-md bg-white dark:bg-gray-800">
                  {
                    typeOperationForm.map((e) => (
                      <label key={e.id} htmlFor={`type_of_operation_${e.id}`} className="w-full block border-b border-gray-300 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-900 p-3 cursor-pointer">
                        <input
                          key={`type_of_operation_${e.id}`}
                          id={`type_of_operation_${e.id}`}
                          type="checkbox"
                          name={`type_of_operation_${e.id}`}
                          className="mr-2"
                          value={e.id}
                          checked={form[`type_of_operation_${e.id}`] || ''}
                          onChange={handleChange}
                        />
                        <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                          {`(${e.id})`}
                          {' '}
                          {e.text}
                        </span>
                      </label>
                    ))
                  }
                </div>
              </div>

              {/* aircraft model */}
              <div className="col-span-2">
                <div className="py-2">
                  <label htmlFor="form-aircraft-model" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                    12) Aircraft Model
                  </label>
                  <input
                    type="text"
                    id="form-aircraft-model"
                    className="t-form"
                    name="aircraft_model"
                    value={form.aircraft_model || ''}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {/* registration mark */}
                  <div className="py-2">
                    <label htmlFor="form-regis-mark" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                      13) Registration Mark
                    </label>
                    <input
                      type="text"
                      id="form-regis-mark"
                      className="t-form"
                      name="regis_mark"
                      value={form.regis_mark || ''}
                      onChange={handleChange}
                      required
                    />
                  </div>
                  {/* serial number */}
                  <div className="py-2">
                    <label htmlFor="form-serial-number" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                      14) Serial Number
                    </label>
                    <input
                      type="text"
                      id="form-serial-number"
                      className="t-form"
                      name="serial_number"
                      value={form.serial_number || ''}
                      onChange={handleChange}
                      required
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {/* state of licensing */}
              <div className="py-2">
                <label htmlFor="form-licensing" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  15) Flight Crew State Of Licensing
                </label>
                <input
                  type="text"
                  id="form-licensing"
                  className="t-form"
                  name="licensing"
                  value={form.licensing || ''}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          </div>
          {/* end general collapse */}

          {/* acknowledge */}
          <div className="py-2 col-span-2">
            <span className="block text-sm font-medium text-gray-700 dark:text-gray-300">16) Acknowledgement Of Receipt *)</span>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <div className="py-2">
                  <label htmlFor="form-ack-name" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                    a) Name
                  </label>
                  <input
                    type="text"
                    id="form-ack-name"
                    className="t-form"
                    name="ack_name"
                    value={form.ack_name || ''}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="py-2">
                  <label htmlFor="form-ack-function" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                    b) Function
                  </label>
                  <input
                    type="text"
                    id="form-ack-function"
                    className="t-form"
                    name="ack_function"
                    value={form.ack_function || ''}
                    onChange={handleChange}
                    required
                  />
                </div>
              </div>
              <div className="py-2">
                <label htmlFor="form-ack-signature" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  c) Signature
                </label>
                <div className="mt-1 dark:bg-gray-900">
                  <img src={form.ack_signature_base64 || ''} alt="ttd" className="border border-gray-300 bg-white" />
                </div>
                <div>
                  <button
                    type="button"
                    className="border-b border-brand-600 text-brand-600 text-sm font-medium"
                    onClick={changeAckSignature}
                  >
                    Change
                  </button>
                </div>
              </div>
            </div>

          </div>
        </div>

        {
          ('id_ramp_inspection' in inspection) ? (
            <div
              className={(tabMenuActive === 'Checklist') ? 'block' : 'hidden'}
            >
              <table className="t-table border border-gray-200 dark:border-gray-700 shadow-sm mt-4">
                <tbody className="t-tbody">
                  {
                    mItem.map((e) => (
                      <tr
                        key={e.id_item}
                        className={`
                          ${(e.id_parent === 0) ? 'table-row' : ''}
                          ${(e.id_parent > 0 && !showChild.includes(e.id_parent)) ? 'hidden' : ''}
                        `}
                      >
                        <td className="t-td w-16">
                          {
                            (parseInt(e.is_has_child, 10) === 1) ? (
                              <button
                                type="button"
                                className="font-bold w-full text-left"
                                onClick={() => toggleChild(e.id_item)}
                              >
                                {e.no}
                              </button>
                            ) : e.no
                          }
                        </td>
                        <td className="t-td">
                          {
                            (parseInt(e.is_has_child, 10) === 1) ? (
                              <button
                                type="button"
                                className="font-bold w-full text-left"
                                onClick={() => toggleChild(e.id_item)}
                              >
                                {e.nama}
                              </button>
                            ) : e.nama
                          }
                        </td>
                        {
                          (parseInt(e.is_has_child, 10) === 1) ? <td className="t-td w-16" /> : ''
                        }
                        {
                          (parseInt(e.is_has_child, 10) === 1) ? (
                            <td className="t-td text-right">
                              <button
                                type="button"
                                onClick={() => toggleChild(e.id_item)}
                              >
                                {
                                  (showChild.includes(e.id_item)) ? (<MinusIcon className="w-5 h-5" />) : (<PlusIcon className="w-5 h-5" />)
                                }
                              </button>
                            </td>
                          ) : ''
                        }
                        {
                          (parseInt(e.is_has_child, 10) === 0) ? (
                            <td className="t-td w-16">
                              <label className="cb-container mb-8">
                                <input
                                  type="checkbox"
                                  name={`${e.id_item}_check`}
                                  checked={formChecklist[`${e.id_item}_check`]}
                                  onChange={handleChangeChecklist}
                                />
                                <span className="checkmark" />
                              </label>
                            </td>
                          ) : ''
                        }
                        {
                          (parseInt(e.is_has_child, 10) === 0) ? (
                            <td className="t-td w-16">
                              <label className="cb-container mb-8">
                                <input
                                  type="checkbox"
                                  name={`${e.id_item}_remark`}
                                  checked={formChecklist[`${e.id_item}_remark`]}
                                  onChange={handleChangeChecklist}
                                />
                                <span className="checkmark" />
                              </label>
                            </td>
                          ) : ''
                        }

                      </tr>
                    ))
                  }

                </tbody>
              </table>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
                {/* action taken */}
                <div className="py-2">
                  <label htmlFor="form-type-of-operation-1" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                    19) Action Taken
                  </label>
                  <div className="mt-1 border border-gray-300 dark:border-gray-700 rounded-md bg-white dark:bg-gray-800">
                    {
                      actionTakenForm.map((e) => (
                        <label key={e.id} htmlFor={`action_taken_${e.id}`} className="w-full block border-b border-gray-300 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-900 p-3 cursor-pointer">
                          <input
                            key={`action_taken_${e.id}`}
                            id={`action_taken_${e.id}`}
                            type="checkbox"
                            name={`action_taken_${e.id}`}
                            className="mr-2"
                            value={e.id}
                            checked={form[`action_taken_${e.id}`] || ''}
                            onChange={handleChange}
                          />
                          <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                            {`(${e.id})`}
                            {' '}
                            {e.text}
                          </span>
                        </label>
                      ))
                    }
                  </div>
                </div>
                {/* remark */}
                <div className="py-2">
                  <div className="grid grid-cols-1 sm:grid-cols-4">
                    <div>
                      <span className="block text-sm font-medium text-gray-700 dark:text-gray-300 px-3">
                        20) Item
                      </span>
                      <span className="block text-sm font-medium text-gray-700 dark:text-gray-300 px-3">
                        21) Cat
                      </span>
                    </div>
                    <span className="col-span-3 block text-sm font-medium text-gray-700 dark:text-gray-300 px-3">
                      22) Remark(s)
                    </span>
                  </div>
                  <div className="border border-gray-300 dark:border-gray-700 rounded-md mt-1 bg-white dark:bg-gray-800">
                    {
                      formChecklistKet.map((e) => (
                        <div
                          key={e.id_item}
                          className="grid grid-cols-1 sm:grid-cols-4 border-b border-gray-300 dark:border-gray-700"
                        >
                          <div>
                            <div className="p-3">
                              <input
                                className="t-form"
                                type="text"
                                value={e.kode}
                                disabled
                              />
                            </div>
                            <div className="p-3">
                              <select
                                className="t-form pr-0 cursor-pointer"
                                style={{
                                  backgroundImage: 'none',
                                }}
                                name={`${e.id_item}_cat`}
                                value={formChecklist[`${e.id_item}_cat`] || ''}
                                onChange={handleChangeChecklist}
                              >

                                <option
                                  value=""
                                >
                                  Select
                                </option>
                                {
                                  actionTakenForm.reverse().map((f) => (
                                    <option
                                      key={f.id}
                                      value={f.id}
                                    >
                                      {f.id}
                                    </option>
                                  ))
                                }
                              </select>
                            </div>
                          </div>
                          <div className="p-3 col-span-3">
                            <textarea
                              className="t-form"
                              name={`${e.id_item}_keterangan`}
                              value={formChecklist[`${e.id_item}_keterangan`] || ''}
                              onChange={handleChangeChecklist}
                            />
                          </div>
                        </div>
                      ))
                    }
                    {
                      (formChecklistKet.length === 0) ? (
                        <div className="p-3 text-center text-sm dark:text-gray-300">NIL</div>
                      ) : ''
                    }
                  </div>
                </div>
              </div>
            </div>
          ) : ''
        }

        {
          ('id_ramp_inspection' in inspection) ? (
            <div
              className={(tabMenuActive === 'Inspector(s) Signature') ? 'block' : 'hidden'}
            >
              {/* inspector name and signature */}
              <div className="py-2 mt-4">
                <div className="w-full md:w-1/2">
                  <div className="flex">
                    <span className="block text-sm font-medium text-gray-700 dark:text-gray-300 px-3">
                      23) Inspector(s) name and signature
                    </span>
                    <button
                      type="button"
                      className="text-sm border-b border-brand-600 text-brand-600 font-medium ml-auto"
                      onClick={() => addInspectorSignature()}
                    >
                      Add
                    </button>
                  </div>
                  <div className="border border-gray-300 dark:border-gray-700 rounded-md mt-1 bg-white dark:bg-gray-800">
                    {
                      inspector.map((e) => (
                        <div key={e.id_ramp_inspection_inspector} className="grid grid-cols-5 border-b border-gray-300 dark:border-gray-700">
                          <span className="col-span-2 block text-sm font-medium text-gray-700 dark:text-gray-300 p-3">
                            {e.name}
                          </span>
                          <div className="col-span-2 p-3">
                            <img src={e.signature || ''} alt="ttd" className="border border-gray-300" />
                          </div>
                          <div className="p-3">
                            <button
                              type="button"
                              className="text-brand-600 px-3"
                              onClick={() => editInspectorSignature(e)}
                            >
                              <PencilIcon className="w-5 h-5" />
                            </button>
                            <button
                              type="button"
                              className="text-red-600 px-3"
                            >
                              <TrashIcon className="w-5 h-5" />
                            </button>

                          </div>
                        </div>
                      ))
                    }
                    {
                      (inspector.length === 0) ? (
                        <div className="text-sm text-center p-3 dark:text-gray-300">
                          NIL
                        </div>
                      ) : ''
                    }
                  </div>
                </div>
              </div>
              {/* crew comment  */}
              <div className="py-2 mt-4">
                <label htmlFor="form-crew-comment" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  24) Crew comments (if any):
                </label>
                <textarea
                  id="form-crew-comments"
                  className="t-form"
                  name="crew_comment"
                  value={form.crew_comment || ''}
                  onChange={handleChange}
                />
              </div>
            </div>
          ) : ''
        }

        <button
          type="submit"
          className="fixed z-100 bottom-8 right-4"
        >
          <FloatingBtn icon="CheckIcon" />
        </button>
      </form>

      <ModalComp
        isOpen={showModalAckSignature}
        modalTitle="Acknowledgement Signature"
        closeModal={() => closeModalAckSignature()}
      >
        <div>
          <div className="border border-gray-300 my-8">
            <SignatureComp
              ref={ackSignatureComp}
            />
          </div>
          <div className="flex gap-2">
            <ButtonComp
              btnColor="green"
              btnText="save"
              handleClick={() => getAckSignature()}
            />
            <ButtonComp
              btnColor="red"
              btnText="close"
              handleClick={() => closeModalAckSignature()}
            />
          </div>
        </div>
      </ModalComp>

      {/* form inspector signature */}
      <ModalComp
        isOpen={showModalInspectorSignature}
        modalTitle="Inspector Signature"
        closeModal={() => closeModalInspectorSignature()}
      >
        <div>
          <div className="border border-gray-300 mt-8">
            <SignatureComp
              ref={inspectorSignatureComp}
            />
          </div>
          <div className="mb-8 mt-4">
            <input
              type="text"
              className="t-form"
              placeholder="Inspectors Name"
              name="name"
              value={formInspector.name || ''}
              onChange={handleChangeInspector}
            />
          </div>
          <div className="flex gap-2">
            <ButtonComp
              btnColor="green"
              btnText="save"
              handleClick={() => getInspectorSignature()}
            />
            <ButtonComp
              btnColor="red"
              btnText="close"
              handleClick={() => closeModalInspectorSignature()}
            />
          </div>
        </div>
      </ModalComp>
    </div>
  )
}
InspectionForm.propTypes = {
  inspection: PropTypes.oneOfType([PropTypes.object]),
  checklist: PropTypes.oneOfType([PropTypes.array]),
  inspector: PropTypes.oneOfType([PropTypes.array]),
  getForm: PropTypes.func.isRequired,
  getFormInspector: PropTypes.func,
}
InspectionForm.defaultProps = {
  inspection: {},
  checklist: [],
  inspector: [],
  getFormInspector: () => null,
}

export default InspectionForm
