import React from 'react'
import PropTypes from 'prop-types'
import { Link, useLocation } from 'react-router-dom'

import {
  HomeIcon, ClipboardListIcon, ChartPieIcon, UserCircleIcon,
} from '@heroicons/react/outline'

function BottomNav({ menu }) {
  const routePath = useLocation().pathname

  function getIcon(icon) {
    switch (icon) {
      case 'HomeIcon':
        return <HomeIcon className="inline h-6 w-6" aria-hidden="true" />
      case 'ClipboardListIcon':
        return <ClipboardListIcon className="inline h-6 w-6" aria-hidden="true" />
      case 'ChartPieIcon':
        return <ChartPieIcon className="inline h-6 w-6" aria-hidden="true" />
      case 'UserCircleIcon':
        return <UserCircleIcon className="inline h-6 w-6" aria-hidden="true" />
      default:
        return <HomeIcon className="inline h-6 w-6" aria-hidden="true" />
    }
  }

  return (
    <div
      className="
        fixed
        w-full
        border-t
        dark:border-gray-700
        py-4
        shadow
        bottom-0
        bg-white
        dark:bg-gray-800
      "
    >
      <div
        className="
        max-w-screen-lg
        mx-auto"
      >
        <div
          className={`
            grid
            grid-cols-${menu.length}
            gap-4
          `}
        >
          {
            menu.map((e) => (
              <Link
                key={e.to}
                to={e.to}
                className="text-center text-xs"
              >
                <div
                  className={`
                    ${(e.to === routePath) ? 'text-brand-700' : 'text-gray-500'}
                  `}
                >
                  {getIcon(e.icon)}
                </div>
                <span
                  className={`
                    ${(e.to === routePath) ? 'text-brand-700' : 'text-gray-500'}
                  `}
                >
                  {e.text}
                </span>
              </Link>
            ))
          }
        </div>
      </div>
    </div>
  )
}
BottomNav.propTypes = {
  menu: PropTypes.oneOfType([PropTypes.array]).isRequired,
}

export default BottomNav
