import { createSlice } from '@reduxjs/toolkit'

export const auth = createSlice({
  name: 'auth',
  initialState: {
    user: null,
    role: [],
    currentRole: null,
    // user: {
    //   id_user: 1,
    //   name: 'Jhonarendra',
    // },
    // role: [
    //   {
    //     id_role: 1,
    //     nama_role: 'Admin',
    //     abilities: 'admin',
    //   },
    //   {
    //     id_role: 2,
    //     nama_role: 'User',
    //     abilities: 'user',
    //   },
    // ],
    // currentRole: {
    //   id_role: 2,
    //   nama_role: 'User',
    //   abilities: 'user',
    // },
  },
  reducers: {
    setUser: (state, val) => {
      state.user = val
    },
    setDummyUser: (state) => {
      state.user = {
        id_user: 1,
        name: 'Jhonarendra',
      }
      state.role = [
        {
          id_role: 1,
          nama_role: 'Admin',
          abilities: 'admin',
        },
        {
          id_role: 2,
          nama_role: 'User',
          abilities: 'user',
        },
      ]
      state.currentRole = {
        id_role: 2,
        nama_role: 'User',
        abilities: 'user',
      }
    },
  },
})

export const { setDummyUser } = auth.actions

export default auth.reducer
