import React from 'react'
import PropTypes from 'prop-types'

function TabMenu({ menu, active, setTabMenuActive }) {
  return (
    <div className="overflow-x-auto">
      <div className="border-b border-gray-200 dark:border-gray-700 whitespace-nowrap">
        <div
          className="-mb-px flex px-4 space-x-8"
          aria-orientation="horizontal"
          role="tablist"
        >
          {
            menu.map((e) => (
              <button
                key={e}
                type="button"
                className={`
                  flex-1
                  whitespace-nowrap
                  py-4
                  px-1
                  border-b
                  text-base
                  font-medium
                  ${
                    (e === active)
                      ? 'text-brand-600 border-brand-600'
                      : 'text-gray-900 dark:text-gray-300 dark:border-gray-700'
                  }
                
                `}
                onClick={() => setTabMenuActive(e)}
                // :class="
                //   e.title === active
                //     ? 'text-indigo-600 border-indigo-600'
                //     : 'text-gray-900 dark:text-gray-300 dark:border-gray-700'
                // "
              >
                { e }
              </button>
            ))
          }
        </div>
      </div>
    </div>
  )
}

TabMenu.propTypes = {
  menu: PropTypes.oneOfType([PropTypes.array]),
  active: PropTypes.string,
  setTabMenuActive: PropTypes.func,
}
TabMenu.defaultProps = {
  menu: ['Menu 1', 'Menu 2'],
  active: 'Menu 1',
  setTabMenuActive: () => null,
}

export default TabMenu
