import { PlusIcon, CheckIcon } from '@heroicons/react/outline'
import React from 'react'
import PropTypes from 'prop-types'

function FloatingBtn({ icon }) {
  function getIcon(e) {
    switch (e) {
      case 'PlusIcon':
        return <PlusIcon className="w-5 h-5 text-white" />
      case 'CheckIcon':
        return <CheckIcon className="w-5 h-5 text-white" />
      default:
        return <PlusIcon className="w-5 h-5 text-white" />
    }
  }
  return (
    <div
      className="
        p-4 rounded-full shadow-lg
        bg-brand-500
      "
    >
      { getIcon(icon)}
    </div>
  )
}

FloatingBtn.propTypes = {
  icon: PropTypes.string,
}
FloatingBtn.defaultProps = {
  icon: 'PlusIcon',
}

export default FloatingBtn
